.modal .modal-content {
  border: none;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.1), 0 2px 4px rgba(0, 0, 0, 0.2);
  border-radius: 0px; }
  .modal .modal-content .status-line {
    height: 6px; }
  .modal .modal-content .modal-header {
    background: #f7f9fa; }

.modal.exception .modal-content .status-line {
  background: #c0392b; }

.modal.exception .modal-content .modal-header {
  color: #c0392b; }

.modal.success .modal-content .status-line {
  background: #62cb31; }

.modal.success .modal-content .modal-header {
  color: #62cb31; }

.modal.warning .modal-content .status-line {
  background: #ffb606; }

.modal.warning .modal-content .modal-header {
  color: #ffb606; }

.modal.info .modal-content .status-line {
  background: #3498db; }

.modal.info .modal-content .modal-header {
  color: #3498db; }
